import React, { Component } from 'react';
import Popupbox, { PopupboxTrigger, PopupboxModal } from 'react-popupbox';
import {PrismCode} from "react-prism";

export default class Demo extends Component {

  onClickTrigger() {
    this.refs.popupbox.openPopupbox();
  }

  render() {
    const popupboxConfig = {
      titleBar: {
        enable: true,
        text: 'Popupbox Demo'
      },
      fadeIn: {
        enable: true,
        speed: 500
      }
    }

    return (
      <div className="demo">
        <div className="demo-default">
          <div className="demo-component">
            <Popupbox ref="popupbox">
              <PopupboxTrigger>
                <span className="popupbox-trigger">Click me</span>
              </PopupboxTrigger>
              <PopupboxModal>
                <p className="quotes">Work like you don't need the money.</p>
                <p className="quotes">Dance like no one is watching.</p>
                <p className="quotes">And love like you've never been hurt.</p>
                <span className="quotes-from">― Mark Twain</span>
              </PopupboxModal>
            </Popupbox>
          </div>
          <div className="demo-source">
            <pre className="prettyprint">
              {`
                render() {
                  <Popupbox ref="popupbox">
                    <PopupboxTrigger>
                      <button>Click me</button>
                    </PopupboxTrigger>
                    <PopupboxModal>
                      <p className="quotes">Work like you don't need the money.</p>
                      <p className="quotes">Dance like no one is watching.</p>
                      <p className="quotes">And love like you've never been hurt.</p>
                      <span className="quotes-from">― Mark Twain</span>
                    </PopupboxModal>
                  </Popupbox>
                }
              `}
            </pre>
          </div>
        </div>

        <div className="demo-trigger">
          <div className="demo-component">
            <p className="demo-componentDescribe">or trigger from outside component ...</p>
            <a className="popupbox-trigger" onClick={this.onClickTrigger.bind(this)}>Click me</a>
          </div>
          <div className="demo-source">
            <pre className="prettyprint">
              {`
                onClickTrigger() {
                  this.refs.popupbox.openPopupbox();
                }

                // trigger
                // <a onClick={this.onClickTrigger.bind(this)}>Click me</a>;
              `}
            </pre>
          </div>
        </div>

        <div className="demo-config">
          <div className="demo-component">
            <p className="demo-componentDescribe">change settings ...</p>
            <Popupbox { ...popupboxConfig }>
              <PopupboxTrigger>
                <span className="popupbox-trigger">Click me</span>
              </PopupboxTrigger>
              <PopupboxModal>
                <img src="static/cat.jpg" />
              </PopupboxModal>
            </Popupbox>
          </div>
          <div className="demo-source">
            <pre className="prettyprint">
              {`
                render() {
                  const popupboxConfig = {
                    titleblar: {
                      enable: true,
                      text: 'Popupbox Demo'
                    },
                    fadeIn: true,
                    fadeInSpeed: 500
                  }

                  return (
                    <Popupbox { ...popupboxConfig }>
                      <PopupboxTrigger>
                        <button>Click me</button>
                      </PopupboxTrigger>
                      <PopupboxModal>
                        <YourContentComponent />
                      </PopupboxModal>
                    </Popupbox>
                  )
                }
              `}
            </pre>
          </div>
        </div>
      </div>
    )
  }
}


